
.owl-theme {
  max-width: 1180px;
  margin-left: -150px;
  margin-top: 105px;
}
.item {
  color: #ffffff;
  width: 330px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition-duration: 1s;
}
.item:hover {
  margin-top: -10px;
  transition: 1s;
  height: 100%;
  cursor: pointer;
}
.owl-stage-outer {
  position: relative;
  -webkit-transform: translate3d(0px, 0px, 0px);
  overflow-x: clip !important;
}
.owl-nav {
  position: absolute;
  top: 0;
  left: -18%;
  color: #fff;
}
.owl-theme .owl-nav [class*='owl-']:hover{
  background-color: transparent;
}
.owl-nav button span{
  font-size: 35px;
  margin: 0;
  padding: 0 19px;
  border: 1px solid #fff;
  border-radius: 50%;
  width: 44px;
  height: 44px;
  display: none;
}
.abc_box .abc {
  color: #fff;
  position: absolute;
  top: 70%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.abc_box .abc::after {
  content: '';
  border-bottom: 2px solid #fff;
  opacity: 0.5;
  display: block;
  width: 200px;
}
.owl-nav button.owl-prev::before {
  content: "";
  background-image: url('../../assets/icons/arrow_left.png');
  border: 1px solid #DADADA;
  width: 44px;
  height: 44px;
  background-size: auto;
  position: absolute;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 100px;
  padding: 20px;
  left: 0px;
  top: 0%;
  right: 0;
  margin: auto;
}
.owl-nav button.owl-next::before {
  content: "";
  background-image: url("../../assets/icons/arrow_right.png");
  border: 1px solid #DADADA;
  width: 44px;
  height: 44px;
  background-size: auto;
  position: absolute;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 100px;
  padding: 20px;
  left: 0px;
  top: 70px;
  right: 0;
  margin: auto;
}

.owl-carousel .owl-item img{
  max-width: 100%;
  width: auto;
}
.owl-carousel .owl-item img{
  display: inline-block;
}


.box {
  width: 100%;
  padding: 10px;
  overflow: hidden;
  background-color: #f5f5f5;
  border-radius: 10px;
}
hr.line {
  margin: 0;
  border: 1px solid #E3E4EB;
}
.names {
  display: flex;
  justify-content: space-between;
  margin: 10px 0;
}
.names p {
  font-family: SegoeUI_bold;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  color: #000000;
  margin: 0;

  img{
    width: 36px;
    height: 36px;
    margin-right: 10px;
  }
}
.names span {
  font-family: SegoeUI_bold;
  font-style: normal;
  font-weight: bold;
  font-size: 15px;
  line-height: 36px;
  color: #EC498A;
}
.lSSlideOuter .lightSlider, .lSSlideOuter .lSPager{
background-color: transparent !important;
}
.slide-img{
height: 207px;
position:relative;
}
.slide-img img{
width:100%;
height: 100%;
object-fit: cover;
box-sizing: border-box;
}
.detail-box{
width: 100%;
box-sizing: border-box;
font-family: calibri;
}
.type{
display: flex;
justify-content: space-between;
}
.type a {
  font-weight: 700;
  letter-spacing: 0.5px;
  padding-right: 8px;
  font-family: SegoeUI_bold;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
  color: #F2994A;
  margin: 14px 0;
}
.type a span{
  font-family: SegoeUI_bold;
font-style: normal;
font-weight: normal;
font-size: 14px;
line-height: 18px;



color: #828282;
}
.time {
  display: flex;
  justify-content: space-between;
}
.time p {
  font-family: SegoeUI_bold;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  color: #000000;
}
.type span.std {
  font-family: SegoeUI_bold;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 40px;
  color: #000000;
}
.type span{
color:rgba(26,26,26,0.5);
}
.price{
color: #333333;
font-weight: 600;
font-size: 1.1rem;
font-family: SegoeUI_bold;
letter-spacing: 0.5px;
}

.buy-btn{
width:160px;
height: 40px;
display: flex;
justify-content: center;
align-items: center;
background-color: #FFFFFF;
color:#252525;
font-weight: 700;
letter-spacing: 1px;
font-family: calibri;
border-radius: 20px;
box-shadow: 2px 2px 30px rgba(0,0,0,0.2);
}
.buy-btn:hover{
color:#FFFFFF;
background-color: #f15fa3;
transition: all ease 0.3s;
}
.overlay{
visibility: hidden;
}
.slide-img:hover .overlay{
visibility: visible;
animation:fade 0.5s;
}

@keyframes fade{
0%{
  opacity: 0;
}
100%{
  opacity: 1;
}
}



.lSSlideWrapper {
  position: static !important;
}
.arrow_box {
  width: 37%;
  height: 432px;
  background-color: #EC498A;
  color: #fff;
  float: left;
  z-index: -1;
}
.lSAction > .lSNext {
  background-position: -32px 0;
left: 212px !important;
  top: 42% !important;
}
.lSAction > .lSPrev {
  left: 216px !important;
}
.lSAction > a {
  top: 36% !important;
  opacity: 1 !important;
}
.tag_btn button.btn.btn-info {
  background: #FEDEFF;
  border-radius: 100px;
