}
.tag_btn h2 {
  font-family: SegoeUI_bold;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  color: #000000;
}
button.btn.btn_Courses {
width: 190px;
height: 56px;
  background: #EC498A;
  border-radius: 20px;
  font-family: SegoeUI_bold;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
